import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IFilter } from 'src/shared/models/type';

@Component({
  selector: 'app-common-filter-modal',
  templateUrl: './common-filter-modal.component.html',
  styleUrls: ['./common-filter-modal.component.scss'],
})
export class CommonFilterModalComponent implements OnInit {
  @Input() data: IFilter[];
  constructor(private readonly modalController: ModalController) {}

  ngOnInit() {}

  async onValidateFilters() {
    console.log('close');
    await this.modalController.dismiss(this.data);
  }

  onUncheckAll() {
    this.data.forEach(elem => (elem.checked = false));
  }
}
