import { Component, OnInit } from '@angular/core';
import { IShopper } from 'src/shared/models/type';
import { DataService } from 'src/shared/services/data.service';
import { BehaviorSubject } from 'rxjs';
import { AppStateService } from 'src/shared/state/app.state.service';
import { StatusBar, Style } from '@capacitor/status-bar';
import { setStyleStatusBar } from 'src/shared/utils/function.utils';

@Component({
  selector: 'app-main',
  templateUrl: './main.page.html',
  styleUrls: ['./main.page.scss'],
})
export class MainPage implements OnInit {
  user$: BehaviorSubject<IShopper>;
  constructor(private readonly appState: AppStateService, private dataService: DataService) {
  }


  ngOnInit() {
    this.dataService.initUserInfos().then((userInfos) => this.user$ = this.appState.getUserInfos());
  }
  ionViewDidEnter() {
    setStyleStatusBar("light")

  }
}
