import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { IFilter } from 'src/shared/models/type';
import { CommonFilterModalComponent } from '../common-filter-modal/common-filter-modal.component';

@Component({
  selector: 'app-search-filter',
  templateUrl: './search-filter.component.html',
  styleUrls: ['./search-filter.component.scss'],
})
export class SearchFilterComponent implements OnInit {
  range = { lower: 50, upper: 350 };
  dataReturned: IFilter[];
  @Input() filters;
  @Input() basicFilters;
  constructor(public modalController: ModalController) {}

  ngOnInit() {}

  rangeChanged(event) {
    console.log(event);
    console.log(this.filters);
    this.range.lower = event.detail.value.lower;
    this.range.upper = event.detail.value.upper;
  }

  async onOpenModal() {
    const modal = await this.modalController.create({
      component: CommonFilterModalComponent,
      cssClass: 'my-custom-modal-css',
      componentProps: {
        data: this.filters,
        backdropDismiss: false,
      },
    });
    modal.onDidDismiss().then(dataReturned => {
      //TODO state management
      if (dataReturned !== null && dataReturned.data !== undefined) {
        this.dataReturned = dataReturned.data.filter(filter => filter.checked);
        //alert('Modal Sent Data :'+ dataReturned);
      } else {
        this.dataReturned = this.filters.filter(filter => filter.checked);
      }
    });
    return await modal.present();
  }

  onRemoveFilter(index: number) {
    console.log(index);
    this.dataReturned.splice(index, 1);
  }
}
