import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PersonalShopperResolver } from 'src/shared/resolver/personal-shopper.resolver';
import { RecoResolver } from 'src/shared/resolver/reco.resolver';
import { WardrobeResolver } from 'src/shared/resolver/wardrobe.resolver';
import { MainPage } from './main.page';
import { AngularFireAuthGuard, redirectUnauthorizedTo } from '@angular/fire/compat/auth-guard';
const redirectUnauthorizedToHome = () => redirectUnauthorizedTo(['/home'])

@NgModule({
  imports: [RouterModule.forChild(MainPageRoutingModule.routes)],
  exports: [RouterModule],
})
export class MainPageRoutingModule {
  static routes: Routes = [
    {
      path: '',
      component: MainPage,
      children: [
        {
          path: 'wardrobe',
          loadChildren: () => import('./wardrobe/wardrobe.module').then(m => m.WardrobePageModule),
          canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },
          resolve: [WardrobeResolver, RecoResolver],
        },
        {
          path: 'personal-shopper',
          loadChildren: () =>
            import('./personal-shopper/personal-shopper.module').then(
              m => m.PersonalShopperPageModule
            ),
          canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },
          resolve: { PersonalShopperResolver },
        },
        {
          path: 'personal-shopper/:error',
          loadChildren: () =>
            import('./personal-shopper/personal-shopper.module').then(
              m => m.PersonalShopperPageModule
            ),
          canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome }
          //resolve: { PersonalShopperResolver, WardrobeResolver },
        },
        {
          path: 'profile',
          loadChildren: () => import('./profile/profile.module').then(m => m.ProfilePageModule),
          canActivate: [AngularFireAuthGuard], data: { authGuardPipe: redirectUnauthorizedToHome },
          resolve: [WardrobeResolver],
        },
        {
          path: '',
          redirectTo: 'personal-shopper',
          pathMatch: 'full',
        },
      ],
    },
  ];
}
