import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable, of } from 'rxjs';
import { take } from 'rxjs/operators';
import { PersonalShopperStateService } from '../state/personal-shopper-state.service';

@Injectable({
  providedIn: 'root',
})
export class PersonalShopperResolver implements Resolve<boolean> {
  constructor(private readonly persoShopperState: PersonalShopperStateService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> {
    if (this.persoShopperState.getAvailableProducts()?.length) {
      return of(true);
    } else {
      return this.persoShopperState.initStore().pipe(take(1));
    }
  }
}
