import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { take } from 'rxjs/operators';
import { WardrobeStateService } from '../state/wardrobe-state.service';

@Injectable({
  providedIn: 'root',
})
export class RecoResolver implements Resolve<boolean> {
  constructor(private readonly wardrobeStateServ: WardrobeStateService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): any {
    if (this.wardrobeStateServ.getRecommendedStyles().value) {
      return true;
    }
    return this.wardrobeStateServ.initRecoStyles().pipe(take(1));
  }
}
