import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AppConstants } from 'src/app/app.constants';
import { IProductHttpResponse } from '../models/http-response.model';
import { IEvent } from '../models/type';

@Injectable({
  providedIn: 'root',
})
export class PersonalShopperCRUDService {
  constructor(private readonly http: HttpClient) { }

  getProductsAsObservable(count = 5, filters: any): Observable<HttpResponse<IProductHttpResponse>> {
    const object = {
      count,
      filters,
    };
    return this.http.post<IProductHttpResponse>(
      AppConstants.productsUrl,
      object,
      { observe: 'response' }
    );
  }

  sendEvent(events: IEvent[]): Promise<any> {
    return this.http.post(AppConstants.eventUrl, { events }).toPromise();
  }

  /*getProductsAsPromise(count = 5): Promise<HttpResponse<IProductHttpResponse>> {
    return this.http.get<IProductHttpResponse>(`https://apidev.younzee.com/api/v1/personnal_shopper_products?count=${count}`, { observe: 'response' }).toPromise();
  }*/
}
