import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { zip } from 'rxjs';
import { map, take } from 'rxjs/operators';
import { WardrobeCategory, WardRobeConstants } from 'src/app/app.constants';
import { WardrobeStateService } from '../state/wardrobe-state.service';
import { isNullOrUndefined } from '../utils/function.utils';

@Injectable({
  providedIn: 'root',
})
export class WardrobeResolver implements Resolve<boolean> {
  constructor(private readonly wardrobeStateServ: WardrobeStateService) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): any {
    if (this.isWardrobeAlreadyInit()) {
      return true;
    }
    return zip(
      this.wardrobeStateServ.initArticlesStore(WardRobeConstants.categoriesIndex),
      this.wardrobeStateServ.initStylesStore()
    ).pipe(
      take(1),
      map(value => value[0] && value[1])
    );
  }

  isWardrobeAlreadyInit(): boolean {
    return !isNullOrUndefined(this.wardrobeStateServ.getAllStyles().value) && !isNullOrUndefined(this.wardrobeStateServ.extractArticlesByCategoryId(WardrobeCategory.ACCESSORIES).value) && !isNullOrUndefined(this.wardrobeStateServ.extractArticlesByCategoryId(WardrobeCategory.CLOTHES).value) && !isNullOrUndefined(this.wardrobeStateServ.extractArticlesByCategoryId(WardrobeCategory.SHOES).value)
  }
}
