import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { PersonalShopperCRUDService } from '../crud/personal-shopper-crud.service';
import { IProduct } from '../models/type';
import { DataService } from '../services/data.service';
import { LoginService } from '../services/login.service';
import { PersonalShopperService } from '../services/personal-shopper.service';
import { AppStateService } from './app.state.service';

@Injectable({
  providedIn: 'root',
})
export class PersonalShopperStateService {
  private availableProducts$ = new BehaviorSubject<IProduct[]>(null);
  constructor(
    private readonly persoShoppingServ: PersonalShopperService,
    private readonly personalShopperCRUD: PersonalShopperCRUDService,
    private readonly router: Router,
    private readonly authServ: LoginService,
    private readonly storage: DataService,
    private readonly appState: AppStateService
  ) { }

  getAvailableProducts(): IProduct[] {
    return this.availableProducts$.value;
  }

  getAvailableProductsAsObservable(): Observable<IProduct[]> {
    return this.availableProducts$
  }
  initStore(): Observable<boolean> {
    const products = [];
    const filters = this.appState.getUserInfos().value.swipe_filter;
    return this.personalShopperCRUD.getProductsAsObservable(5, filters).pipe(
      map((response: any) => {
        response.body.data.forEach(product => {
          const imagesArray = [];
          product.images.forEach(image => {
            imagesArray.push(image.s3_presigned_url);
          });
          products.push({
            id: product.id,
            visible: true,
            detailsVisible: false,
            name: product.description,
            brand: product.brand_id,
            price: `${product.price} €`,
            like: 0,
            view: 0,
            images: imagesArray,
          });
        });
        this.availableProducts$.next(products);
        return true;
      }),
      catchError(err => {
        this.router.navigate(['/main/personal-shopper', err.status]);
        return of(true);
      })
    );
  }
  addNewProducts(): Observable<boolean> {
    const products = this.getAvailableProducts();
    const filters = this.appState.getUserInfos().value.swipe_filter ? this.appState.getUserInfos().value.swipe_filter : '';
    return this.personalShopperCRUD.getProductsAsObservable(5, filters).pipe(
      map((response: any) => {
        response.body.data.forEach(product => {
          const imagesArray = [];
          product.images.forEach(image => {
            imagesArray.push(image.s3_presigned_url);
          });
          products.push({
            id: product.id,
            visible: true,
            name: product.description,
            brand: product.brand_id,
            price: `${product.price} €`,
            like: 0,
            view: 0,
            images: imagesArray,
          });
        });
        this.availableProducts$.next(products);
        return true;
      }),
      catchError(err => {
        this.router.navigate(['/main/personal-shopper', err.status]);
        return of(true);
      })
    );
  }

  resetAvailableProducts() {
    this.availableProducts$.next(null)
  }
}
